import SeriesHistory from "@domain/core/series/data/SeriesHistory";
export default class GetSeriesHistoryResponse {
    constructor({ title, latest, posts }) {
        this.title = title;
        this.latest = latest;
        this.posts = posts;
    }
    isRead(title) {
        return this.posts.includes(title);
    }
    static fromSeriesHistory(seriesHistory) {
        return new GetSeriesHistoryResponse({
            title: seriesHistory.title,
            latest: seriesHistory.latest,
            posts: seriesHistory.posts
        });
    }
    static toSeriesHistory(getSeriesHistoryResponse) {
        return new SeriesHistory({
            title: getSeriesHistoryResponse.title,
            latest: getSeriesHistoryResponse.latest,
            posts: getSeriesHistoryResponse.posts
        });
    }
}
;
