import titleToUrl from "@common/utils/titleToUrl";
export default class GetArticleResponse {
    constructor({ title, description, published, tags }) {
        this.title = title;
        this.url = titleToUrl(title);
        this.description = description;
        this.published = published;
        this.tags = tags;
    }
    static fromArticle(article) {
        return new GetArticleResponse({
            title: article.title,
            description: article.description,
            published: article.published,
            tags: article.tags
        });
    }
}
;
