    import { gtag } from "@presentation/common/google-analytics/gtag";
    import Layout from "@pages/Layout.svelte";
    import ListContainer from "@series/view/components/ListContainer.svelte";
    import WarningPannel from "@series/view/components/WarningPannel.svelte";

    $: document.title = "Show Yourself - Series";

    onMount(() => {
        getOriginalSeriesList();
        getBookmarkedSeriesList();
        getRecentSeriesList();
        getUpcomingSeriesList();

        gtag();
    });
</script>

<WarningPannel />
<Layout>
    <div class="series">
        <ListContainer items={$originalSeriesObserver} category={SERIES_CATEGORY.ORIGINAL} />
        <ListContainer items={$recentSeriesObserver} category={SERIES_CATEGORY.INPROGRESS} />
        <ListContainer items={$bookmarkedSeriesObserver} category={SERIES_CATEGORY.FAVORITE} />
        <ListContainer items={$upcomingSeriesObserver} category={SERIES_CATEGORY.UPCOMING} />
    </div>
</Layout>

