import GetFullscreenStateResponse from "@domain/core/component/usecase/data/GetFullscreenStateResponse";
export default class GetFullScreenStateImpl {
    constructor(componentRepository) {
        this.componentRepository = componentRepository;
        this.KEY = "FULLSCREEN";
    }
    async execute() {
        const result = await this.componentRepository.get(this.KEY);
        return (result !== null)
            ? new GetFullscreenStateResponse({ value: result })
            : new GetFullscreenStateResponse({ value: false });
    }
}
;
