<script lang="ts">
    export let totalPageCount: number;
    export let pageNumber: number;

    import { isNumber } from "@common/utils/type";
    import { push } from "svelte-spa-router";

    $: textValue = pageNumber;
    $: totalPageCount = totalPageCount;

    function onKeyEnter(e: KeyboardEvent): void  {
        if (e.key === 'Enter') {
            if (!isNumber(textValue.toString()) || textValue > totalPageCount) {
                textValue = 1;
            }
            push(`/article/${textValue}`);
        }
    }
</script>

<div class="paging">
    <div class="nav prev">
        {#if pageNumber > 1}
            <a href="/#/article/{pageNumber - 1}">이전</a>
        {:else}
            <span>이전</span>
        {/if}
    </div>
    <div class="page">
        <span><input type="text" bind:value={textValue} on:keydown={onKeyEnter}></span>
        <span>/</span>
        <span>{totalPageCount}</span>
    </div>
    <div class="nav next">
        {#if pageNumber < totalPageCount}
            <a href="/#/article/{pageNumber + 1}">다음</a>
        {:else}
            <span>다음</span>
        {/if}
    </div>
</div>

