<script lang="ts">
    export let params = {};

    import { onMount } from "svelte";
    import type GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
    import { getSeriesByUrl } from "@series/viewmodel/SeriesViewModel";
    import NotFound from "@pages/NotFound.svelte";
    import SeriesDetail from "@series/view/SeriesDetail.svelte";

    let series: GetSeriesResponse | null = null;

    onMount(async () => {
        // @ts-ignore
        const url = decodeURI(params.groups.series).replaceAll("/", "");
        series = await getSeriesByUrl({url});
    });
</script>

{#if series === null}
    <NotFound />
{:else}
    <SeriesDetail series={series} />
{/if}
