import { articleUseCase } from "@src/inject";
import { writable } from "svelte/store";
let selectedTags = [];
export const allTagListObserver = writable([]);
export const selectedTagListObserver = writable([]);
function updateTagList() {
    selectedTagListObserver.set(selectedTags);
    const names = selectedTags.map(tag => tag.name);
    allTagListObserver.update(allTagList => {
        return allTagList.map(tag => {
            if (names.includes(tag.name)) {
                tag.selected = true;
            }
            else {
                tag.selected = false;
            }
            return tag;
        });
    });
}
export async function getAllTagList() {
    const response = await articleUseCase.getAllTagList.execute();
    allTagListObserver.set(response.tags);
    updateTagList();
}
;
export function addSelectedTag(tag) {
    if (!selectedTags.includes(tag)) {
        selectedTags.push(tag);
    }
    updateTagList();
}
;
export function removeSelectedTag(tag) {
    const idx = selectedTags.findIndex(selected => selected === tag);
    if (idx > -1) {
        selectedTags.splice(idx, 1);
    }
    updateTagList();
}
;
export function toggleSelectedTag(tag) {
    if (tag.selected) {
        removeSelectedTag(tag);
    }
    else {
        addSelectedTag(tag);
    }
}
;
export function resetSelectedTagList() {
    selectedTags = [];
    updateTagList();
}
;
