import SeriesHistoryEntity from "@domain/core/series/data/SeriesHistory";
export default class SeriesHistory {
    constructor({ title, latest, posts }) {
        this.title = title;
        this.latest = latest;
        this.posts = posts;
    }
    static toSeriesHistory(seriesHistoryDao) {
        return new SeriesHistoryEntity({
            title: seriesHistoryDao.title,
            latest: seriesHistoryDao.latest,
            posts: seriesHistoryDao.posts
        });
    }
    static fromSeriesHistory(seriesHistory) {
        return new SeriesHistory({
            title: seriesHistory.title,
            latest: seriesHistory.latest,
            posts: seriesHistory.posts
        });
    }
}
;
