import { isTypeOf } from "@common/utils/type";
import Result from "@domain/common/usecase/data/Result";
import GetArticleResponse from "@domain/core/article/usecase/data/GetArticleResponse";
export default class GetArticlePageListImpl {
    constructor(articleRepository) {
        this.articleRepository = articleRepository;
    }
    async execute(query) {
        return new Promise(async (resolve, reject) => {
            const articleReponse = await this.fetch(query.data, query.pageOptions);
            if (!articleReponse.pageOptions) {
                reject(`article page options not exist`);
            }
            else {
                const articleDtoList = (await articleReponse).data.map(article => GetArticleResponse.fromArticle(article));
                const result = new Result({
                    data: articleDtoList,
                    pageOptions: articleReponse.pageOptions
                });
                resolve(result);
            }
        });
    }
    ;
    async fetch(data, pageOptions) {
        let articleReponse;
        if (data) {
            if (isTypeOf(data, "tags")) {
                articleReponse = await this.articleRepository.findByTags(data.tags, pageOptions);
            }
            else {
                articleReponse = await this.articleRepository.findByUrls([data.url], pageOptions);
            }
        }
        else {
            articleReponse = await this.articleRepository.findAll(pageOptions);
        }
        return articleReponse;
    }
}
;
