export default class DeleteAllRecentsImpl {
    constructor(seriesRepository, seiresHistoryRepository) {
        this.seriesRepository = seriesRepository;
        this.seiresHistoryRepository = seiresHistoryRepository;
    }
    async execute() {
        this.seriesRepository.deleteAllRecents();
        this.seiresHistoryRepository.deleteAll();
    }
}
;
