export default class AddRecentImpl {
    constructor(seriesRepository) {
        this.seriesRepository = seriesRepository;
        this.RECENT_MAX_COUNT = 4;
    }
    async execute(command) {
        const recents = await this.seriesRepository.findAllRecents();
        const updatedRecents = this.insertRecentsUtillMaxSize(command.title, recents);
        this.seriesRepository.updateRecents(updatedRecents);
    }
    insertRecentsUtillMaxSize(title, recents) {
        const splicedRecents = this.spliceRecentIfExist(title, recents);
        const popedRecents = this.popIfExceedMaxCount(splicedRecents);
        popedRecents.push(title);
        return popedRecents;
    }
    spliceRecentIfExist(title, recents) {
        const idx = recents.indexOf(title);
        if (idx > -1) {
            recents.splice(idx, 1);
        }
        return recents;
    }
    popIfExceedMaxCount(recents) {
        if (recents.length >= this.RECENT_MAX_COUNT) {
            recents.pop();
        }
        return recents;
    }
}
;
