export default function Paging() {
    function findPageOptions(args) {
        for (const idx in args) {
            const arg = args[idx];
            if (arg instanceof Object) {
                const obj = arg;
                if (obj.hasOwnProperty("pageNumber") && obj.hasOwnProperty("pageSize")) {
                    return obj;
                }
            }
        }
    }
    return function (classPrototype, methodName, descriptor) {
        const originalMethod = descriptor.value;
        descriptor.value = async function (...args) {
            const result = await originalMethod.apply(this, args);
            const pageOptions = findPageOptions(args);
            if (pageOptions) {
                const articleResponse = result;
                const startIndex = pageOptions.pageNumber * pageOptions.pageSize;
                const size = pageOptions.pageSize;
                const totalCount = articleResponse.data.length;
                return {
                    data: articleResponse.data.splice(startIndex, size),
                    pageOptions: {
                        pageNumber: pageOptions.pageNumber,
                        pageSize: pageOptions.pageSize,
                        totalCount: totalCount,
                        totalPageCount: Math.ceil(totalCount / size)
                    }
                };
            }
            return result;
        };
    };
}
;
