import findIndex from "lodash/findIndex";
import LocalStorageStore from "@datastore/store/LocalStorageStore";
import BookmarkEntity from "@datastore/repository/entity/Bookmark";
export default class BookmarkRepositoryImpl {
    constructor() {
        this.store = new LocalStorageStore();
        this.prefix = "BOOKMARK_";
    }
    findByType(type) {
        const result = this.getBookmarkDaoListByType(type);
        return new Promise((resolve) => {
            resolve(result.map(dao => BookmarkEntity.toBookmark(dao)));
        });
    }
    update(bookmark) {
        const bookmarks = this.getBookmarkDaoListByType(bookmark.type);
        const idx = findIndex(bookmarks, { 'title': bookmark.title });
        if (idx > -1) {
            bookmarks.splice(idx, 1);
        }
        bookmarks.push(BookmarkEntity.fromBookmark(bookmark));
        this.store.set(this.prefix + bookmark.type, JSON.stringify(bookmarks));
    }
    deleteByTitleAndType(title, type) {
        const bookmarks = this.getBookmarkDaoListByType(type);
        const idx = findIndex(bookmarks, { 'title': title });
        if (idx > -1) {
            bookmarks.splice(idx, 1);
        }
        this.store.set(this.prefix + type, JSON.stringify(bookmarks));
    }
    deleteAllByType(type) {
        this.store.delete(this.prefix + type);
    }
    getBookmarkDaoListByType(type) {
        const json = this.store.get(this.prefix + type);
        return (json === null) ? [] : JSON.parse(json);
    }
}
;
