<script>
    import configuration from '@src/configuration';
    import Logo from '@components/navigation/Logo.svelte';
    import ItemList from '@components/navigation/ItemList.svelte';
    import LinkList from '@components/navigation/LinkList.svelte';

    let isExpanded = false;
    const maxWidth = 768;
    let windowWidth;

    function toggleExpanded() {
        if (windowWidth <= maxWidth) {
            isExpanded = !isExpanded;
        }
    }

    function onWindowResize() {
        if (windowWidth > maxWidth) {
            isExpanded = false;
        }
    }
</script>

<svelte:window bind:innerWidth={windowWidth} on:resize="{onWindowResize}" />

<nav class:active={isExpanded} on:click="{toggleExpanded}">
    <div>
        <Logo />
    </div>
    <div>
        <ItemList items={configuration.nav} />
    </div>
    <div class="{isExpanded ? 'active' : 'hide'}">
        <LinkList />
    </div>
</nav>

<style lang="scss">
    $width: 768px;
    $navHeight: 64px;
    $navPadding: 8%;

    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 64px;
        overflow: hidden;
        background-color: #fff;
        border-bottom: solid 1px #ddd;
        z-index: 9999;

        &>div:first-child {
            padding-left: $navPadding;
        }

        &>div:last-child {
            padding-right: $navPadding;
        }
    }

    @media (max-width: $width) {
        nav {
            position: relative;
            flex-direction: column;
            justify-content: flex-start;
            height: 56px;
            padding: 0;
            cursor: pointer;
            border-bottom: solid 1px #ddd;

            &.active {
                position: fixed;
                height: 100vh;
            }

            &>div:first-child {
                padding-left: 0;
            }

            &>div:last-child {
                padding-right: 0;
            }
        }
    }
</style>
