import SeriesHistory from "@domain/core/series/data/SeriesHistory";
export default class SetPostToReadImpl {
    constructor(seriesHistoryRepository) {
        this.seriesHistoryRepository = seriesHistoryRepository;
    }
    async execute(command) {
        let seriesHistory = await this.seriesHistoryRepository.findBySeriesTitle(command.seriesTitle);
        if (!seriesHistory) {
            seriesHistory = new SeriesHistory({
                title: command.seriesTitle,
                latest: null,
                posts: []
            });
        }
        seriesHistory.setLatestPost(command.postTitle);
        seriesHistory.addReadPost(command.postTitle);
        this.seriesHistoryRepository.update(seriesHistory);
    }
}
;
