const getArticle = new GetArticleImpl(articleRepository);
const getArticleList = new GetArticleListImp(articleRepository);
const getArticlePageList = new GetArticlePageListImpl(articleRepository);
const getArticleComponent = new GetArticleComponentImpl(import("@res/articles.ref.svelte"));
const getAllTagList = new GetAllTagListImpl(articleRepository);
export const articleUseCaseDefinitions = {
    getArticle,
    getArticleList,
    getArticlePageList,
    getArticleComponent,
    getAllTagList
};
