<script lang="ts">
    export let category: SERIES_CATEGORY;
    export let items: GetSeriesResponse[];

    import { deleteAllRecents } from "@series/viewmodel/SeriesViewModel";
    import SERIES_CATEGORY from "@domain/core/series/usecase/data/SeriesCategory";
    import type GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
    import List from "@series/view/components/List.svelte";

    let wrap: HTMLElement;
    let isSticky = false;

    function onScroll() {
        if (wrap !== undefined && wrap !== null) {
            let wrapTop = wrap.offsetTop;
            let windowY = window.scrollY;

            if (windowY > wrapTop) {
                isSticky = true;
            } else {
                isSticky = false;
            }
        }
    }
</script>

<svelte:window on:scroll={onScroll} />

{#if items.length !== 0}
    <div bind:this={wrap}>
        <div class:sticky={isSticky} class="category">
            <div class="title">
                <div>
                    {category}
                </div>
                <div>
                    {#if category === SERIES_CATEGORY.INPROGRESS}
                        <div class="d" on:click={() => deleteAllRecents() }>모든 기록 지우기</div>
                    {/if}
                </div>
            </div>
        </div>
        <List items={items} category={category} />
    </div>
{/if}

<style lang="scss">
    $width: 768px;

    .category {
        position: sticky;
        top: 0;
        font-weight: 700;
        font-size: 1.5rem;
        vertical-align: middle;
        background-color: var(--backdrop-bg-color);
        backdrop-filter: var(--backdrop-value);
        z-index: 999;
        height: 5rem;
        display: flex;
        flex-direction: column;
        transition: all .2s ease;

        .title {
            padding: 0 8%;
            height: 5rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &::after {
            width: 100%;
            height: 1px;
            background-color: #fff;
            content: "";
            display: block;
        }

        .d {
            font-size: 1rem;
            font-weight: 700;
            margin-left: 1rem;
            padding: 1rem;
            border-radius: 5px;
            cursor: pointer;
            display: none;
            transition: all .2s ease;

            &:hover {
                background-color: #efefef;
            }
        }

        &.sticky {
            &::after {
                background-color: #ddd;
            }

            .d {
                display: block;
            }
        }
    }

    @media (max-width: $width) {
        .category {
            padding: 0 0;
            background-color: #fff;
            backdrop-filter: none;
            font-size: 1.25rem;

            .title {
                padding: 0 4%;
            }
        }
    }
</style>
