import LocalStorageStore from "@datastore/store/LocalStorageStore";
export default class ComponentRepositoryImpl {
    constructor() {
        this.store = new LocalStorageStore();
        this.prefix = "COMPONENT_";
    }
    set(key, value) {
        this.store.set(this.prefix + key, JSON.stringify(value));
    }
    get(key) {
        const result = this.store.get(this.prefix + key);
        const value = (result === null) ? null : JSON.parse(result);
        return new Promise((resolve) => {
            resolve(value);
        });
    }
}
;
