<script lang="ts">
import { componentUseCase } from "@src/inject";

    export let buttonText: string = "닫기";
    export let storable: boolean = false;
    export let key: string | null = null;

    import { onMount } from "svelte";
    import { writable } from "svelte/store";

    let closed = writable(false);

    onMount(async () => {
        if (storable) {
            if (key == null) {
                throw Error("Storable Pannel key must not be null.");
            }
            const response = await componentUseCase.getPannelState.execute({id: key});
            closed.set(response.value);
        }
    });

    function onClose() {
        if (storable && key !== null) {
            componentUseCase.setPannelState.execute({id: key, value: true});
        }
        closed.set(true);
    }
</script>

<div class="pannel" class:closed={$closed}>
    <div>
        <span class="text">
            <slot />
        </span>
        <!-- svelte-ignore a11y-missing-attribute -->
        <a class="close" on:click={onClose}>{buttonText}</a>
    </div>
</div>

<style lang="scss">
    .pannel {
        height: 50px;
        background-color: #efefef;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: solid 1px #ddd;
        border-top: solid 1px #ddd;
        font-size: 1rem;
        padding: 0 2rem;

        &.closed {
            display: none;
        }

        .text {
            margin-right: .5rem;
        }

        .close {
            white-space: nowrap;
            cursor: pointer;
        }
    }
</style>
