<script lang="ts">
    export let series: GetSeriesResponse;

    import titleToUrl from "@common/utils/titleToUrl";
    import { push } from "svelte-spa-router";
    import type GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
    import { getLatestPostBySeriesTtitle } from "@series/viewmodel/SeriesViewModel";
    import Button from "@components/button/Button.svelte";
    import Play from "svelte-bootstrap-icons/lib/PlayFill/PlayFill.svelte";

    let latest = getLatestPostBySeriesTtitle({title: series.title});

    function onResume(post: string) {
        push(`/series/${series.url}/${titleToUrl(post)}`);
    }

    function onPlay() {
        push(`/series/${series.url}/${titleToUrl(series.contents[0].title)}`);
    }
</script>

{#await latest}
    <div></div>
{:then post} 
    {#if post}
        <Button icon={Play} size={24} color="#333" onClick={() => onResume(post)}>이어보기</Button>
    {:else}
        <Button icon={Play} size={24} color="#333" onClick={onPlay}>첫 글 보기</Button>
    {/if}
{/await}
