import GetSeriesComponentResponse from "@domain/core/series/usecase/data/GetSeriesComponentResponse";
export default class GetSeriesComponentImpl {
    // unknown because post can be svelte, vue, react...
    // it must convert to each types on view model.
    constructor(seriesRefs) {
        this.seriesRefs = seriesRefs;
    }
    async execute(query) {
        const component = await this.fetch(query.seriesTitle, query.postTitle);
        return new Promise((resolve, reject) => {
            if (component !== null) {
                resolve(new GetSeriesComponentResponse({
                    component: component
                }));
            }
            else {
                reject(`${query.seriesTitle} ${query.postTitle} not exist in series reference.`);
            }
        });
    }
    // TODO: define error.
    async fetch(series, post) {
        const refs = (await this.seriesRefs).default;
        const ref = refs[series];
        if (!ref) {
            return null;
        }
        const idx = ref.findIndex(([title, _]) => title === post);
        if (idx > -1) {
            return ref[idx][1];
        }
        return null;
    }
}
;
