export default class SeriesHistory {
    constructor({ title, latest, posts }) {
        this.title = title;
        this.latest = latest;
        this.posts = posts;
    }
    setLatestPost(title) {
        this.latest = title;
    }
    addReadPost(title) {
        if (!this.posts.includes(title)) {
            this.posts.push(title);
        }
    }
}
;
