<script>
    export let src = null;
    export let alt = "image";
    export let aspectRatio = null;
    export let bgColor = "#f3f3f3";
    export let magnify=1;

    import Placeholder from "@components/image/Placeholder.svelte";

    const defaultWidth = 200;
    let width = 200;

    $: logoSize = Math.min(4, (1.1 * (width / defaultWidth)).toFixed(3) * magnify);
    $: textSize = Math.min(2.5, (0.7 * (width / defaultWidth)).toFixed(3) * magnify);

    function preload(src) {
        return new Promise((resolve) => {
            let img = new Image();
            img.onload = resolve;
            img.src = src;
        });
    }
</script>

<div class="wrap" style:aspect-ratio="{aspectRatio}" bind:clientWidth={width}>
    {#if src}
        {#await preload(src)}
            <Placeholder bgColor={bgColor} logoSize={logoSize} textSize={textSize} />
        {:then _}
            <img src="{src}" alt={alt} />
        {:catch}
            <Placeholder bgColor={bgColor} logoSize={logoSize} textSize={textSize} />
        {/await}
    {:else}
        <Placeholder bgColor={bgColor} logoSize={logoSize} textSize={textSize} />
    {/if}
</div>

<style lang="scss">
    .wrap {
        display: flex;
        overflow: hidden;
    }

    img {
        width: 100%;
        object-fit: cover;
    }
</style>
