import BOOKMARK_TYPE from "@domain/core/bookmark/data/BookmarkType";
import GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
export default class GetBookmarkedSeriesListImpl {
    constructor(seriesRepository, bookmarkRepository) {
        this.seriesRepository = seriesRepository;
        this.bookmarkRepository = bookmarkRepository;
    }
    async execute() {
        const bookmarked = await this.bookmarkRepository.findByType(BOOKMARK_TYPE.SERIES);
        const titles = bookmarked.map(bookmark => bookmark.title);
        const series = await this.seriesRepository.findByTitles(titles);
        return series.map(series => GetSeriesResponse.fromSeries(series));
    }
}
;
