import GetArticleComponentQuery from "@domain/core/article/usecase/data/GetArticleComponentQuery";
import GetArticlePageListQuery from "@domain/core/article/usecase/data/GetArticlePageListQuery";
import GetArticleQuery from "@domain/core/article/usecase/data/GetArticleQuery";
import { articleUseCase } from "@src/inject";
import { get, writable } from "svelte/store";
const defaultPageOptions = {
    pageNumber: 0,
    pageSize: 10,
    totalCount: 0,
    totalPageCount: 0
};
export const articleObserver = writable([]);
export const pageObserver = writable(defaultPageOptions);
export async function getArticleByUrl(url) {
    const query = new GetArticleQuery({
        data: { url: url }
    });
    return articleUseCase.getArticle.execute(query);
}
;
export async function getArticleListByTags(tags) {
    const tagList = tags.map(tag => tag.name);
    const query = new GetArticlePageListQuery({
        data: { tags: tagList },
        pageOptions: get(pageObserver)
    });
    const articleResponse = await articleUseCase.getArticlePageList.execute(query);
    articleObserver.set(articleResponse.data);
    pageObserver.set(articleResponse.pageOptions);
}
;
export async function getArticleComponentByUrl(url) {
    const article = await articleUseCase.getArticle.execute(new GetArticleQuery({
        data: { url: url }
    }));
    return (await articleUseCase.getArticleComponent.execute(new GetArticleComponentQuery({
        title: article.title
    }))).component;
}
;
export function setPage(pageNumber) {
    pageObserver.update(pageOptions => {
        pageOptions.pageNumber = pageNumber;
        return pageOptions;
    });
}
;
export function resetPage() {
    pageObserver.set(defaultPageOptions);
}
;
