import BookmarkEntity from "@domain/core/bookmark/data/Bookmark";
export default class Bookmark {
    constructor({ title, type }) {
        this.title = title;
        this.type = type;
    }
    static fromBookmark(bookmark) {
        return new Bookmark({
            title: bookmark.title,
            type: bookmark.type
        });
    }
    static toBookmark(bookmarkDao) {
        return new BookmarkEntity({
            title: bookmarkDao.title,
            type: bookmarkDao.type
        });
    }
}
;
