import Series from "@domain/core/series/data/Series";
import SeriesContent from "@domain/core/series/data/SeriesContent";
import SeriesRequirement from "@domain/core/series/data/SeriesRequirement";
export default class RawSeries {
    constructor({ title, intro, description, status, contents, requires, recommends, thumbnail, published }) {
        this.title = title;
        this.intro = intro;
        this.description = description;
        this.status = status;
        this.contents = contents;
        this.requires = requires;
        this.recommends = recommends;
        this.thumbnail = thumbnail;
        this.published = published;
    }
    static toSeries(rawSeries) {
        return new Series({
            title: rawSeries.title,
            intro: rawSeries.intro,
            description: rawSeries.description,
            status: rawSeries.status,
            contents: rawSeries.contents.map((content, idx) => new SeriesContent({
                id: idx,
                title: content.title
            })),
            requires: rawSeries.requires.map(require => new SeriesRequirement({
                text: require.text,
                link: (require.link) ? require.link : null
            })),
            recommends: rawSeries.recommends,
            thumbnail: rawSeries.thumbnail,
            published: rawSeries.published.toString()
        });
    }
}
;
