<script lang="ts">
    export let item: GetSeriesResponse;

    import type GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
    import type GetSeriesHistoryResponse from "@domain/core/series/usecase/data/GetSeriesHistoryResponse";
    import { getHistory } from "@series/viewmodel/SeriesViewModel";
    import CheckLarge from "svelte-bootstrap-icons/lib/CheckLg/CheckLg.svelte";

    const promise: Promise<GetSeriesHistoryResponse> = getHistory({title: item.title});
</script>

<ul>
    {#await promise}
        <li></li>
    {:then history}
        {#each item.contents as content, index}
            <li class:read={history.isRead(content.title)}>
                <a href="/#/series/{item.url}/{content.url}">
                    <div>
                        <span class="i">{index + 1}. </span>
                        <span class="t">{content.title}</span>
                    </div>
                    <div class:read={history.isRead(content.title)} class="a">
                        <CheckLarge width="18" height="18" />
                    </div>
                </a>
            </li>
    {/each}
    {/await}
</ul>

