import { seriesUseCase, bookmarkUseCase } from "@src/inject";
import { writable } from "svelte/store";
import BOOKMARK_TYPE from "@domain/core/bookmark/data/BookmarkType";
import DeleteAllBookmarksCommand from "@domain/core/bookmark/usecase/data/DeleteAllBookmarksCommand";
import DeleteBookmarkCommand from "@domain/core/bookmark/usecase/data/DeleteBookmarkCommand";
import AddBookmarkCommand from "@domain/core/bookmark/usecase/data/AddBookmarkCommand";
import GetSeriesQuery from "@domain/core/series/usecase/data/GetSeriesQuery";
import AddRecentCommand from "@domain/core/series/usecase/data/AddRecentCommand";
import GetHistoryQuery from "@domain/core/series/usecase/data/GetHistoryQuery";
import SetPostToReadCommand from "@domain/core/series/usecase/data/SetPostToReadCommand";
const BookmarkType = BOOKMARK_TYPE.SERIES;
export const originalSeriesObserver = writable([]);
export const bookmarkedSeriesObserver = writable([]);
export const recentSeriesObserver = writable([]);
export const upcomingSeriesObserver = writable([]);
export async function getSeriesByUrl({ url }) {
    return seriesUseCase.getSeries.execute(new GetSeriesQuery({
        data: { url }
    }));
}
;
export async function getSeriesByTitle({ title }) {
    return seriesUseCase.getSeries.execute(new GetSeriesQuery({
        data: { title }
    }));
}
;
export async function getPostBySeriesTitleAndPostIndex(title, postIndex) {
    const series = await seriesUseCase.getSeries.execute(new GetSeriesQuery({
        data: { title }
    }));
    const post = series.contents[postIndex];
    return (await seriesUseCase.getSeriesComponent.execute({ seriesTitle: series.title, postTitle: post.title })).component;
}
;
export async function getOriginalSeriesList() {
    const series = await seriesUseCase.getOriginalSeriesList.execute();
    originalSeriesObserver.set(series);
}
;
export async function getBookmarkedSeriesList() {
    const series = await seriesUseCase.getBookmarkedSeriesList.execute();
    bookmarkedSeriesObserver.set(series);
}
;
export async function getRecentSeriesList() {
    const series = await seriesUseCase.getRecentSeriesList.execute();
    recentSeriesObserver.set(series);
}
;
export async function getUpcomingSeriesList() {
    const series = await seriesUseCase.getUpcomingSeriesList.execute();
    upcomingSeriesObserver.set(series);
}
;
export async function addBookmark({ title }) {
    bookmarkUseCase.addBookmark.execute(new AddBookmarkCommand({
        title: title,
        type: BookmarkType
    }));
    getBookmarkedSeriesList();
}
;
export async function deleteBookmark({ title }) {
    bookmarkUseCase.deleteBookmark.execute(new DeleteBookmarkCommand({
        title: title, type: BookmarkType
    }));
    getBookmarkedSeriesList();
}
;
export async function deleteAllBookmarks() {
    bookmarkUseCase.deleteAllBookmarks.execute(new DeleteAllBookmarksCommand({
        type: BookmarkType
    }));
    getBookmarkedSeriesList();
}
;
export async function addRecent({ title }) {
    seriesUseCase.addRecent.execute(new AddRecentCommand({
        title
    }));
    getRecentSeriesList();
}
;
export async function deleteAllRecents() {
    seriesUseCase.deleteAllRecents.execute();
    getRecentSeriesList();
}
;
export async function getHistory({ title }) {
    return seriesUseCase.getHistory.execute(new GetHistoryQuery({
        title
    }));
}
;
export async function getLatestPostBySeriesTtitle({ title }) {
    const history = await seriesUseCase.getHistory.execute(new GetHistoryQuery({
        title
    }));
    return history.latest;
}
;
export async function setPostToRead({ seriesTitle, postTitle }) {
    seriesUseCase.setPostToRead.execute(new SetPostToReadCommand({
        seriesTitle,
        postTitle
    }));
}
;
