<script lang="ts">
    export let params = {};

    import { onMount } from "svelte";
    import type GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
    import { getPostBySeriesTitleAndPostIndex, getSeriesByUrl } from "@series/viewmodel/SeriesViewModel";
    import Layout from "@pages/Layout.svelte";
    import NotFound from "@pages/NotFound.svelte";

    let series: GetSeriesResponse | null;
    let post: typeof import("*.svelte");

    // @ts-ignore
    $: seriesUrl = decodeURI(params.groups.series);
    // @ts-ignore
    $: postUrl = decodeURI(params.groups.post);

    onMount(() => {
        onHashChange();
    });

    async function onHashChange() {
        series = await getSeriesByUrl({url: seriesUrl});
        if (series !== null) {
            const index = series.getPostIndexByPostUrl(postUrl);
            post = await getPostBySeriesTitleAndPostIndex(series.title, index);
        }
    }
</script>

<svelte:window on:hashchange={onHashChange} />

{#if series}
    {#await post}
        <Layout center={true}>
            포스트 불러오는 중...
        </Layout>
    {:then component}
        {#if component}
            <svelte:component this={component.default} series={series} title={seriesUrl} subtitle={postUrl}>
            </svelte:component>
        {:else}
            <NotFound />
        {/if}
    {/await}
{:else}
    <NotFound />
{/if}
