import Home from "@pages/home/Home.svelte";
import Article from "@pages/article/view/Article.svelte";
import ArticlePost from "@pages/article/view/Post.svelte";
import Series from "@pages/series/view/Series.svelte";
import SeriesDetail from "@pages/series/view/Detail.svelte";
import SeriesPost from "@pages/series/view/Post.svelte";
import Project from "@pages/project/Project.svelte";
import Contact from "@pages/contact/Contact.svelte";
import NotFound from "@pages/NotFound.svelte";
const routes = new Map();
routes.set("/", Home);
routes.set("/article", Article);
routes.set(/^\/article\/(?<page>[0-9]+)/, Article);
routes.set(/^\/article\/(?<title>(.*)+)$/, ArticlePost);
routes.set("/series", Series);
routes.set(/^\/series\/(?<series>[^\/(.*)]+)\/(?<post>[^\/(.*)]+)+/, SeriesPost);
routes.set(/^\/series\/(?<series>(.*)+)(\/?)$/, SeriesDetail);
routes.set("/project", Project);
routes.set("/contact", Contact);
routes.set("*", NotFound);
export default routes;
