import { isTypeOf } from "@common/utils/type";
import GetArticleResponse from "@domain/core/article/usecase/data/GetArticleResponse";
export default class GetArticleListImp {
    constructor(articleRepository) {
        this.articleRepository = articleRepository;
    }
    async execute(query) {
        let articles = await this.fetch(query);
        return new Promise((resolve) => {
            const articleDtoList = articles.map(article => GetArticleResponse.fromArticle(article));
            resolve(articleDtoList);
        });
    }
    async fetch(query) {
        let articleReponse;
        if (query.data) {
            if (isTypeOf(query.data, "tags")) {
                articleReponse = await this.articleRepository.findByTags(query.data.tags);
            }
            else {
                articleReponse = await this.articleRepository.findByUrls([query.data.url]);
            }
        }
        else {
            articleReponse = await this.articleRepository.findAll();
        }
        return articleReponse.data;
    }
}
;
