import titleToUrl from "@common/utils/titleToUrl";
import SERIES_STATUS from "@domain/core/series/data/SeriesStatus";
export default class GetSeriesResponse {
    constructor({ title, intro, description, status, thumbnail, published, contents, requires, recommends }) {
        this.title = title;
        this.url = titleToUrl(title);
        this.intro = intro;
        this.description = description;
        this.status = status;
        this.thumbnail = thumbnail;
        this.published = published;
        this.contents = contents;
        this.requires = requires;
        this.recommends = recommends;
    }
    static fromSeries(series) {
        return new GetSeriesResponse({
            title: series.title,
            intro: series.intro,
            description: series.description,
            status: series.status,
            thumbnail: series.thumbnail,
            published: series.published,
            contents: series.contents,
            requires: series.requires,
            recommends: series.recommends
        });
    }
    isDone() {
        return this.status === SERIES_STATUS.DONE;
    }
    isOngoing() {
        return this.status === SERIES_STATUS.ONGOING;
    }
    isUpcoming() {
        return this.status === SERIES_STATUS.UPCOMING;
    }
    getPostIndexByPostUrl(url) {
        return this.contents.findIndex(content => content.url === url);
    }
}
;
