import LocalStorageStore from "@datastore/store/LocalStorageStore";
import SeriesHistoryEntity from "@datastore/repository/entity/SeriesHistory";
export default class SeriesHistoryRepositoryImpl {
    constructor() {
        this.store = new LocalStorageStore();
        this.historyKey = "SERIES_HISTORY";
    }
    async findAll() {
        const histories = this.getHistoriesFromStore();
        return histories.map(history => SeriesHistoryEntity.toSeriesHistory(history));
    }
    async findBySeriesTitle(title) {
        const histories = this.getHistoriesFromStore();
        const idx = histories.findIndex(history => history.title === title);
        let seriesHistory = null;
        if (idx > -1) {
            seriesHistory = SeriesHistoryEntity.toSeriesHistory(histories[idx]);
        }
        return seriesHistory;
    }
    update(history) {
        const histories = this.getHistoriesFromStore();
        const idx = histories.findIndex(history => history.title === history.title);
        if (idx > -1) {
            histories[idx] = SeriesHistoryEntity.fromSeriesHistory(history);
        }
        else {
            histories.push(SeriesHistoryEntity.fromSeriesHistory(history));
        }
        this.store.set(this.historyKey, JSON.stringify(histories));
    }
    updateAll(histories) {
        const converted = histories.map(history => SeriesHistoryEntity.fromSeriesHistory(history));
        this.store.set(this.historyKey, JSON.stringify(converted));
    }
    deleteAll() {
        this.store.delete(this.historyKey);
    }
    getHistoriesFromStore() {
        const result = this.store.get(this.historyKey);
        return (result === null) ? [] : JSON.parse(result);
    }
}
;
