import GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
export default class GetRecentSeriesListImpl {
    constructor(seriesRepository) {
        this.seriesRepository = seriesRepository;
    }
    async execute() {
        const recents = await this.seriesRepository.findAllRecents();
        const series = await this.seriesRepository.findByTitles(recents);
        return series.map(series => GetSeriesResponse.fromSeries(series));
    }
}
;
