<script lang="ts">
    export let item: GetSeriesResponse;

    import { onMount } from "svelte";
    import { push } from "svelte-spa-router";
    import titleToUrl from "@common/utils/titleToUrl";
    import type GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
    import { getHistory } from "@series/viewmodel/SeriesViewModel";
    import Image from "@components/image/Image.svelte";
    import Thumbnail from "@series/view/components/Thumbnail.svelte";

    let progress = 0;
    let post: string;

    onMount(async () => {
        const history = await getHistory({title: item.title});

        if (history.latest) {
            post = history.latest;
        } else {
            post = history.posts[0];
        }

        const readCount = Object.entries(item.contents).filter(([_, content]) => history.isRead(content.title)).length;
        progress = (readCount / item.contents.length) * 100;
    });

    function onMenuClick() {
        push(`/series/${item.url}/${titleToUrl(post)}`);
    }
</script>

<Thumbnail item={item} showContents={false} showMenu={true} onMenuClick={onMenuClick} showProgress={true} progressValue={progress}>
    <div slot="image">
        <Image src={item.thumbnail} aspectRatio={4/3} bgColor="#fff" />
    </div>
    <div slot="menu-title">
        이어보기
    </div>
    <div slot="latest">
        {post}
    </div>
</Thumbnail>
