import GetPannelStateResponse from "@domain/core/component/usecase/data/GetPannelStateReponse";
export default class GetPannelStateImpl {
    constructor(componentRepository) {
        this.componentRepository = componentRepository;
        this.KEY = "PANNEL";
    }
    async execute(query) {
        const result = await this.componentRepository.get(`${this.KEY}_${query.id}`);
        return (result !== null)
            ? new GetPannelStateResponse({
                value: result
            })
            : new GetPannelStateResponse({
                value: false
            });
    }
}
;
