import { isTypeOf } from "@common/utils/type";
import GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
export default class GetSeriesImpl {
    constructor(seriesRepository) {
        this.seriesRepository = seriesRepository;
    }
    async execute(query) {
        let series;
        if (isTypeOf(query.data, "title")) {
            series = await this.seriesRepository.findByTitles([query.data.title]);
        }
        else {
            series = await this.seriesRepository.findByUrls([query.data.url]);
        }
        return new Promise((resolve, reject) => {
            if (series.length > 0) {
                resolve(GetSeriesResponse.fromSeries(series[0]));
            }
            else {
                reject(`${query.data} not exist`);
            }
        });
    }
}
;
