import SERIES_STATUS from "@domain/core/series/data/SeriesStatus";
import GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
export default class GetOriginalSeriesListImpl {
    constructor(seriesRepository) {
        this.seriesRepository = seriesRepository;
    }
    async execute() {
        const doneSeries = this.seriesRepository.findByStatus(SERIES_STATUS.DONE);
        const ongoingSeries = this.seriesRepository.findByStatus(SERIES_STATUS.ONGOING);
        const seriesList = await Promise.all([doneSeries, ongoingSeries]);
        return [...seriesList.flatMap(list => list.map(series => GetSeriesResponse.fromSeries(series)))];
    }
}
;
