<script lang="ts">
    export let series: GetSeriesResponse;

    import { onDestroy, onMount } from "svelte";
    import type GetSeriesResponse from "@domain/core/series/usecase/data/GetSeriesResponse";
    import { gtag } from "@presentation/common/google-analytics/gtag";
    import Image from "@components/image/Image.svelte";
    import Layout from "@pages/Layout.svelte";
    import PlayButton from "@series/view/components/PlayButton.svelte";
    import BookmarkButton from "@series/view/components/BookmarkButton.svelte";
    import Date from "@series/view/components/Date.svelte";
    import Requires from "@series/view/components/Requires.svelte";
    import Recommends from "@series/view/components/Recommends.svelte";
    import Warn from "@series/view/components/Warn.svelte";
    import Contents from "@series/view/components/Contents.svelte";
    import StatusBadge from "@series/view/components/StatusBadge.svelte";

    $: document.title = series.title;

    let innerWidth: number;
    let elNavigation: HTMLElement | null;
    let observer: ResizeObserver;

    onMount(async () => {
        elNavigation = document.querySelector("nav");
        if (elNavigation !== null) {
            onWindowSizeChanged();
            observer = new ResizeObserver(onWindowSizeChanged);
            observer.observe(elNavigation);
        }

        gtag();
    });

    onDestroy(() => {
        if (elNavigation !== null) {
            observer.unobserve(elNavigation);
            observer.disconnect();
        }
    });

    function onWindowSizeChanged() {
        if (elNavigation !== null) {
            if (innerWidth > 768) {
                elNavigation.style.position = "fixed";
            } else {
                elNavigation.style.position = "relative";
            }
        }
    }
</script>

<svelte:window bind:innerWidth={innerWidth} />

<Layout>
    <div class="detail">
        <div class="col col-1">
            <div class="row row-1">
                <div class="thumbnail">
                    <div class="wrap">
                        <div class="border">
                            <Image src={series.thumbnail} aspectRatio=1 bgColor="#fff" magnify=1.25 />
                        </div>
                    </div>
                </div>
                <div class="btn-wrap">
                        <BookmarkButton title={series.title}/>
                        {#if !series.isUpcoming()}
                            <PlayButton series={series} />
                        {/if}
                    <div class="date">
                        <Date item={series} />
                    </div>
                </div>
            </div>
            <div class="row row-2">
                <div class="title">
                    <div class="t">
                        <div>
                            {series.title}
                            {#if series.isOngoing()}
                                <StatusBadge series={series} />
                            {/if}
                        </div>
                    </div>
                </div>
                <div class="content-wrap">
                    <div class="description">
                        {@html series.description}
                    </div>
                    <div class="requires">
                        <Requires requires={series.requires} />
                    </div>
                    <div class="recommends">
                        <Recommends recommends={series.recommends} />
                    </div>
                    <div class="warn">
                        <Warn item={series} />
                    </div>
                    <div class="contents">
                        <Contents item={series} />
                    </div>
                </div>
            </div>
        </div>
    </div>
</Layout>

