import Article from "@domain/core/article/data/Article";
export default class RawArticle {
    constructor({ title, description, published, tags }) {
        this.title = title;
        this.description = description;
        this.published = published;
        this.tags = tags;
    }
    static toArticle(rawArticle) {
        return new Article({
            title: rawArticle.title,
            description: rawArticle.description,
            published: rawArticle.published.toString(),
            tags: rawArticle.tags
        });
    }
}
;
