<script lang="ts">
    export let params = {};

    import { onMount } from "svelte";
    import type GetArticleResponse from "@domain/core/article/usecase/data/GetArticleResponse";
    import { getArticleByUrl, getArticleComponentByUrl } from "@article/viewmodel/ArticleViewModel";
    import Layout from "@pages/Layout.svelte";
    import NotFound from "@pages/NotFound.svelte";

    let articles: Promise<[GetArticleResponse, typeof import("*.svelte")]>;

    onMount(() => {
        onHashChange();
    });

    function onHashChange() {
        // @ts-ignore
        const url = decodeURI(params.groups.title);
        const articlePromise = getArticleByUrl(url);
        const svelteComponentPromise = getArticleComponentByUrl(url);
        articles = Promise.all([articlePromise, svelteComponentPromise]);
    }
</script>

<svelte:window on:hashchange={onHashChange} />

{#await articles}
    <Layout center={true}>
        포스트 불러오는 중...
    </Layout>
{:then component}
    {#if component != null}
        <svelte:component this={component[1].default} article={component[0]}>
        </svelte:component>
    {/if}
{:catch e}
    <NotFound />
{/await}
