<script lang="ts">
    export let title: string;

    import { onMount } from "svelte";
    import {
        bookmarkedSeriesObserver,
        getBookmarkedSeriesList,
        addBookmark,
        deleteBookmark
    } from "@series/viewmodel/SeriesViewModel";
    import Button from "@components/button/Button.svelte";
    import Bookmark from "svelte-bootstrap-icons/lib/BookmarkStar/BookmarkStar.svelte";
    import BookmarkFill from "svelte-bootstrap-icons/lib/BookmarkStarFill/BookmarkStarFill.svelte";

    let isBookmarked = false;

    bookmarkedSeriesObserver.subscribe(bookmarks => {
        const idx = bookmarks.findIndex(bookmark => bookmark.title === title);
        (idx > -1) ? isBookmarked = true : isBookmarked = false;
    });

    onMount(async () => {
        getBookmarkedSeriesList();
    });
</script>

{#if isBookmarked}
    <Button icon={BookmarkFill} size={16} color="#c90505" onClick={() => deleteBookmark({title})}>즐겨찾기에서 제거</Button>
{:else}
    <Button icon={Bookmark} size={16} color="#c90505" onClick={() => addBookmark({title})}>즐겨찾기에 추가</Button>
{/if}
