import GetArticleComponentResponse from "@domain/core/article/usecase/data/GetArticleComponentResponse";
export default class GetArticleComponentImpl {
    // unknown because post can be svelte, vue, react...
    // it must convert to each types on view model.
    constructor(articleRefs) {
        this.articleRefs = articleRefs;
    }
    async execute(query) {
        const component = await this.fetch(query.title);
        return new Promise((resolve, reject) => {
            if (component !== null) {
                resolve(new GetArticleComponentResponse({
                    component: component
                }));
            }
            else {
                reject(`${query.title} not exist in article reference.`);
            }
        });
    }
    async fetch(title) {
        const refs = (await this.articleRefs).default;
        const idx = refs.findIndex(ref => ref[0] === title);
        if (idx > -1) {
            return refs[idx][1];
        }
        return null;
    }
}
;
