import Tag from "@domain/core/article/usecase/data/Tag";
import GetAllTagListResponse from "@domain/core/article/usecase/data/GetAllTagListResponse";
export default class GetAllTagListImpl {
    constructor(articleRepository) {
        this.articleRepository = articleRepository;
    }
    async execute() {
        const tags = await this.articleRepository.findAllTags();
        return this.getTagDtoList(tags);
    }
    getTagDtoList(tags) {
        const promiseList = tags.map(tag => {
            return new Promise(async (resolve) => {
                const value = await this.articleRepository.findArticleCountByTag(tag);
                resolve(new Tag({
                    name: tag,
                    value: value,
                    selected: false
                }));
            });
        });
        return new Promise(async (resolve) => {
            resolve(new GetAllTagListResponse({
                tags: await Promise.all(promiseList)
            }));
        });
    }
}
;
