    import SERIES_CATEGORY from "@domain/core/series/usecase/data/SeriesCategory";
    import ListItem from "@series/view/components/ListItem.svelte";
    import InprogressItem from "@series/view/components/InProgressListItem.svelte";
    import FavoriteListItem from "@series/view/components/FavoriteListItem.svelte";
    import UpcomingListItem from "@series/view/components/UpComingListItem.svelte";
