    <ul>
        {#each items as item}
            <li class="series-list-item">
                <a href="/#/series/{item.url}">
                    {#if category === SERIES_CATEGORY.ORIGINAL}
                        <ListItem item={item} />
                    {:else if category === SERIES_CATEGORY.INPROGRESS}
                        <InprogressItem item={item} />
                    {:else if category === SERIES_CATEGORY.FAVORITE}
                        <FavoriteListItem item={item} />
                    {:else}
                        <UpcomingListItem item={item} />
                    {/if}
                </a>
            </li>
        {/each}
