<script lang="ts">
    export let params = {};

    import { onMount } from "svelte";
    import { push } from "svelte-spa-router";
    import { 
        articleObserver,
        getArticleListByTags,
        pageObserver,
        setPage
    } from "@article/viewmodel/ArticleViewModel";
    import { selectedTagListObserver } from "@article/viewmodel/TagViewModel";
    import { gtag } from "@presentation/common/google-analytics/gtag";
    import Layout from "@pages/Layout.svelte";
    import Tag from "@article/view/components/Tag.svelte";
    import List from "@article/view/components/List.svelte";
    import Paging from "@article/view/components/Paging.svelte";

    $: document.title = "Show Yourself - Article";
    // @ts-ignore
    $: currentPage = (params.groups !== undefined && params.groups.page !== undefined) ? parseInt(params.groups.page) : 1;
    $: totalPageCount = $pageObserver.totalPageCount;

    onMount(() => {
        onHashChange();
        gtag();
    });

    function onHashChange() {
        if (currentPage > totalPageCount) {
            toFirstPage();
        } else {
            setPage(currentPage - 1);
        }
        getArticleListByTags($selectedTagListObserver);
    }

    // TODO: optimize update article list when hash, tag changed.
    selectedTagListObserver.subscribe(_ => {
        toFirstPage();
        getArticleListByTags($selectedTagListObserver);
    });

    function toFirstPage() {
        setPage(0);
        push(`/article/1`);
    }
</script>

<svelte:window on:hashchange={onHashChange} />

<Layout>
    <div class="article">
        <div class="col-1">
            <Tag />
        </div>
        <div class="col-2">
            <List articles={$articleObserver} />
        </div>
        <div class="col-3">
            <Paging totalPageCount={totalPageCount} pageNumber={currentPage} />
        </div>
    </div>
</Layout>

<style lang="scss">
    $width: 768px;

    .article {
        padding: 0 8%;

        .col-1 {
            position: sticky;
            top: 0;
            background-color: #fff;
            padding-top: 1.5rem;
            padding-bottom: 1rem;
            border-bottom: solid 1px #ddd;
        }

        .col-3 {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 3rem;
        }
    }

    @media (max-width: $width) {
        .article {
            padding: 0;

            .col-1 {
                padding: .75rem 4% .25rem;
            }

            .col-2 {
                margin-top: 0;
                border-bottom: solid 1px #ddd;
            }

            .col-3 {
                margin-top: 2rem;
            }
        }
    }
</style>
