import GetSeriesHistoryResponse from "@domain/core/series/usecase/data/GetSeriesHistoryResponse";
export default class GetHistoryImpl {
    constructor(seriesHistoryRepository) {
        this.seriesHistoryRepository = seriesHistoryRepository;
    }
    async execute(query) {
        const seriesHistory = await this.seriesHistoryRepository.findBySeriesTitle(query.title);
        let response;
        if (!seriesHistory) {
            response = new GetSeriesHistoryResponse({
                title: query.title,
                latest: null,
                posts: []
            });
        }
        else {
            response = GetSeriesHistoryResponse.fromSeriesHistory(seriesHistory);
        }
        return response;
    }
}
;
