import RawSeries from "@datastore/repository/entity/RawSeries";
import LocalStorageStore from "@datastore/store/LocalStorageStore";
import titleToUrl from "@common/utils/titleToUrl";
export default class SeriesRepositoryImpl {
    constructor() {
        this.store = new LocalStorageStore();
        this.recentKey = "SERIES_RECENT";
        this.pendingSeries = import("@res/series");
    }
    async findAll() {
        return this.getSeries();
    }
    async findByStatus(status) {
        const series = await this.getSeries();
        return this.filterSeries(series, (series) => series.status === status);
    }
    async findByTitles(titles) {
        const series = await this.getSeries();
        return this.filterSeries(series, (series) => titles.includes(series.title));
    }
    async findByUrls(urls) {
        const series = await this.getSeries();
        return this.filterSeries(series, (series) => urls.includes(titleToUrl(series.title)));
    }
    findAllRecents() {
        const result = this.store.get(this.recentKey);
        const value = (result === null) ? [] : JSON.parse(result);
        return new Promise((resolve) => {
            resolve(value);
        });
    }
    updateRecents(recents) {
        this.store.set(this.recentKey, JSON.stringify(recents));
    }
    deleteAllRecents() {
        this.store.delete(this.recentKey);
    }
    async getSeries() {
        const raws = (await this.pendingSeries).default;
        return this.convertRawSeriesToSeries(raws);
    }
    convertRawSeriesToSeries(rawSeries) {
        return rawSeries.map(series => RawSeries.toSeries(series));
    }
    filterSeries(series, predicate) {
        return series.filter((s) => predicate(s));
    }
}
;
